import React, { useEffect, useState, useRef } from "react";
import { useDispatch, useSelector } from "react-redux";
import { connect } from "./redux/blockchain/blockchainActions";
import { fetchData } from "./redux/data/dataActions";
import * as s from "./styles/globalStyles";
import styled from "styled-components";

const truncate = (input, len) =>
  input.length > len ? `${input.substring(0, len)}...` : input;

export const StyledButton = styled.button`
  padding: 10px;
  border-radius: 50px;
  border: none;
  background-color: var(--secondary);
  padding: 10px;
  font-weight: bold;
  color: var(--secondary-text);
  width: 100px;
  cursor: pointer;
  box-shadow: 0px 6px 0px -2px rgba(250, 250, 250, 0.3);
  -webkit-box-shadow: 0px 6px 0px -2px rgba(250, 250, 250, 0.3);
  -moz-box-shadow: 0px 6px 0px -2px rgba(250, 250, 250, 0.3);
  :active {
    box-shadow: none;
    -webkit-box-shadow: none;
    -moz-box-shadow: none;
  }
`;

export const StyledRoundButton = styled.button`
  padding: 10px;
  border-radius: 100%;
  border: none;
  background-color: var(--secondary);
  padding: 10px;
  font-weight: bold;
  font-size: 15px;
  color: var(--secondary-text);
  width: 30px;
  height: 30px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0px 4px 0px -2px rgba(250, 250, 250, 0.3);
  -webkit-box-shadow: 0px 4px 0px -2px rgba(250, 250, 250, 0.3);
  -moz-box-shadow: 0px 4px 0px -2px rgba(250, 250, 250, 0.3);
  :active {
    box-shadow: none;
    -webkit-box-shadow: none;
    -moz-box-shadow: none;
  }
`;

export const ResponsiveBanner = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: stretched;
  align-items: center;
  width: 100%;
  @media (min-width: 767px) {
    flex-direction: column;
  }
`;

export const ResponsiveWrapper = styled.div`
  display: flex;
  flex: 1;
  flex-direction: column;
  justify-content: stretched;
  align-items: stretched;
  width: 100%;
  @media (min-width: 767px) {
    flex-direction: row;
  }
`;

export const ResponsiveCrapper = styled.div`
  display: flex;
  max-width: 80%;
  @media (min-width: 767px) {
    flex-direction: row;
  }
`;

export const StyledImg = styled.img`
  box-shadow: 0px 5px 11px 2px rgba(0, 0, 0, 0.7);
  border: 4px solid var(--secondary);
  background-color: var(--accent);
  border-radius: 100%;
  width: 100px;
  @media (min-width: 900px) {
    width: 100px;
  }
  @media (min-width: 1000px) {
    width: 150px;
  }
  transition: width 0.5s;
`;

export const StyledLogo = styled.img`
  width: 200px;
  @media (min-width: 767px) {
    width: 800px;
  }
  transition: width 0.5s;
  transition: height 0.5s;
`;

export const StyledLink = styled.a`
  color: var(--secondary);
  text-decoration: none;
  font-weight: bold;
`;

function App() {
  const dispatch = useDispatch();
  const blockchain = useSelector((state) => state.blockchain);
  const data = useSelector((state) => state.data);
  const [claimingNft, setClaimingNft] = useState(false);
  const [feedback, setFeedback] = useState(``);
  const [mintAmount, setMintAmount] = useState(1);
  const [CONFIG, SET_CONFIG] = useState({
    CONTRACT_ADDRESS: "",
    SCAN_LINK: "",
    NETWORK: {
      NAME: "",
      SYMBOL: "",
      ID: 0,
    },
    NFT_NAME: "",
    SYMBOL: "",
    MAX_SUPPLY: 1,
    WEI_COST: 0,
    DISPLAY_COST: 0,
    GAS_LIMIT: 0,
    MARKETPLACE: "",
    MARKETPLACE_LINK: "",
    SHOW_BACKGROUND: false,
  });

  const claimNFTs = () => {
    let cost = CONFIG.WEI_COST;
    let gasLimit = CONFIG.GAS_LIMIT;
    let totalCostWei = String(cost * mintAmount);
    let totalGasLimit = String(gasLimit * mintAmount);
    console.log("Cost: ", totalCostWei);
    console.log("Gas limit: ", totalGasLimit);
    setFeedback(`Minting your ${CONFIG.NFT_NAME}...`);
    setClaimingNft(true);
    blockchain.smartContract.methods
      .mint(mintAmount)
      .send({
        gasLimit: String(totalGasLimit),
        to: CONFIG.CONTRACT_ADDRESS,
        from: blockchain.account,
        value: totalCostWei,
      })
      .once("error", (err) => {
        console.log(err);
        setFeedback("Sorry, something went wrong please try again later.");
        setClaimingNft(false);
      })
      .then((receipt) => {
        console.log(receipt);
        setFeedback(
          `WOW, the ${CONFIG.NFT_NAME} is yours! go visit Opensea.io to view it.`
        );
        setClaimingNft(false);
        dispatch(fetchData(blockchain.account));
      });
  };

  const decrementMintAmount = () => {
    let newMintAmount = mintAmount - 1;
    if (newMintAmount < 1) {
      newMintAmount = 1;
    }
    setMintAmount(newMintAmount);
  };

  const incrementMintAmount = () => {
    let newMintAmount = mintAmount + 1;
    if (newMintAmount > 10) {
      newMintAmount = 10;
    }
    setMintAmount(newMintAmount);
  };

  const getData = () => {
    if (blockchain.account !== "" && blockchain.smartContract !== null) {
      dispatch(fetchData(blockchain.account));
    }
  };

  const getConfig = async () => {
    const configResponse = await fetch("/config/config.json", {
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
      },
    });
    const config = await configResponse.json();
    SET_CONFIG(config);
  };

  useEffect(() => {
    getConfig();
  });

  useEffect(() => {
    getData();
  }, [blockchain.account]);

  return (
    <s.Screen>
      <s.Container
        flex={1}
        ai={"center"}
        style={{ padding: 24, backgroundColor: "var(--primary)" }}
        image={CONFIG.SHOW_BACKGROUND ? "/config/images/bg.png" : null}
      >
        <ResponsiveBanner
          flex={1}
          style={{ padding: 5, "align-content": "center" }}
          test
        >
          <StyledLogo alt={"logo"} src={"/config/images/logo.png"} />
          <s.SpacerSmall />
          <s.Container fd={"column"} ai={"center"}>
            {
              //Number(data.totalSupply) >= CONFIG.MAX_SUPPLY
            }
            {Number(data.totalSupply) >= CONFIG.MAX_SUPPLY ? (
              <s.BoxContainer fd={"column"} ai={"center"}>
                <s.TextTitle
                  style={{ textAlign: "center", color: "var(--accent-text)" }}
                >
                  The sale has ended.
                </s.TextTitle>
                <s.TextDescription
                  style={{ textAlign: "center", color: "var(--accent-text)" }}
                >
                  You can still find {CONFIG.NFT_NAME} on{" "}
                  <StyledLink target={"_blank"} href={CONFIG.MARKETPLACE_LINK}>
                    {CONFIG.MARKETPLACE}.io
                  </StyledLink>
                </s.TextDescription>
              </s.BoxContainer>
            ) : (
              <s.BoxContainer
                fd={"row"}
                ai={"center"}
                jc={"center"}
                width={"800px"}
              >
                {blockchain.account === "" ||
                blockchain.smartContract === null ? (
                  <>
                    <s.SpacerSmall />
                    <s.Container ai={"center"} jc={"center"} fd={"row"}>
                      <StyledButton
                        onClick={(e) => {
                          e.preventDefault();
                          dispatch(connect());
                          getData();
                        }}
                      >
                        CONNECT
                      </StyledButton>
                    </s.Container>
                    <></>
                  </>
                ) : (
                  <>
                    <s.SpacerSmall />
                    <s.Container ai={"center"} jc={"center"} fd={"row"}>
                      <StyledButton
                        disabled={claimingNft ? 1 : 0}
                        onClick={(e) => {
                          e.preventDefault();
                          claimNFTs();
                          getData();
                        }}
                      >
                        {claimingNft ? "BUSY" : "BUY"}
                      </StyledButton>
                      <s.SpacerMedium />
                      <s.TextTitle
                        style={{
                          textAlign: "start",
                          fontSize: 30,
                          color: "var(--accent-text)",
                          "text-shadow": "5px 5px 5px black",
                        }}
                      >
                        {data.totalSupply} / {CONFIG.MAX_SUPPLY}
                      </s.TextTitle>
                      <s.SpacerMedium />
                      <StyledRoundButton
                        style={{ lineHeight: 0.4 }}
                        disabled={claimingNft ? 1 : 0}
                        onClick={(e) => {
                          e.preventDefault();
                          decrementMintAmount();
                        }}
                      >
                        -
                      </StyledRoundButton>
                      <s.SpacerMedium />
                      <s.TextDescription
                        style={{
                          textAlign: "center",
                          color: "var(--accent-text)",
                          "text-shadow": "3px 3px 3px black",
                        }}
                      >
                        {mintAmount}
                      </s.TextDescription>
                      <s.SpacerMedium />
                      <StyledRoundButton
                        disabled={claimingNft ? 1 : 0}
                        onClick={(e) => {
                          e.preventDefault();
                          incrementMintAmount();
                        }}
                      >
                        +
                      </StyledRoundButton>
                    </s.Container>
                    <></>
                  </>
                )}
                <s.SpacerSmall />
                <s.TextTitle
                  style={{
                    textAlign: "start",
                    fontSize: 30,
                    color: "var(--accent-text)",
                    "text-shadow": "5px 5px 5px black",
                  }}
                >
                  {(CONFIG.DISPLAY_COST * mintAmount).toFixed(3)}{" "}
                  {CONFIG.NETWORK.SYMBOL}
                </s.TextTitle>
              </s.BoxContainer>
            )}
          </s.Container>
          <s.BoxContainer fd={"row"} width={"800px"}>
            <s.SpacerMedium />
            <s.TextDescription
              style={{
                textAlign: "center",
                color: "var(--accent-text)",
              }}
            >
              {feedback}
            </s.TextDescription>
          </s.BoxContainer>
        </ResponsiveBanner>
        {blockchain.errorMsg !== "" ? (
          <>
            <s.TextDescription
              style={{
                textAlign: "center",
                color: "var(--accent-text)",
              }}
            >
              {blockchain.errorMsg}
            </s.TextDescription>
            <s.SpacerLarge />
            <></>
          </>
        ) : null}
        <s.SpacerMedium />
        <s.BoxContainer fd={"row"} jc={"space-evenly"} width={"800px"}>
          <StyledLink target={"_blank"} href={CONFIG.MARKETPLACE_LINK}>
            <StyledImg alt={"num1"} src={"/config/images/num1.gif"} />
          </StyledLink>
          <StyledLink target={"_blank"} href={CONFIG.MARKETPLACE_LINK}>
            <StyledImg alt={"num2"} src={"/config/images/num2.gif"} />
          </StyledLink>
          <StyledLink target={"_blank"} href={CONFIG.MARKETPLACE_LINK}>
            <StyledImg alt={"num3"} src={"/config/images/num3.gif"} />
          </StyledLink>
          <StyledLink target={"_blank"} href={CONFIG.MARKETPLACE_LINK}>
            <StyledImg alt={"num4"} src={"/config/images/num4.gif"} />
          </StyledLink>
        </s.BoxContainer>
        <s.SpacerLarge />
        <s.TextDescription
          style={{
            textAlign: "center",
            color: "var(--primary-text)",
            width: "75%",
            "font-size": "30px",
            "text-shadow": "5px 5px 5px black",
          }}
        >
          Crypto Space Muffins are ambitious baked goods taking muffinkind’s
          first step into the unknown universe. Not too long ago, the muffin's
          home planet of Muffin-z 42069 was invaded by the evil Lord Burner of
          planet Overcooked-69. With Lord Burner’s Space Oven of Death™ , our
          peaceful muffin’s home planet was burnt to the point of no return,
          forcing our yummy friends to venture out to distant galaxies. Through
          minting, your solar systems coordinates are transmitted along the
          blockchain to help guide a lost muffin to your amazing safe planet.
        </s.TextDescription>
        <s.SpacerSmall />
        <s.TextDescription
          style={{
            textAlign: "center",
            color: "var(--primary-text)",
            "text-shadow": "3px 3px 3px black",
          }}
        >
          Once minted, feel free to use your muffin in any way. Commercial
          included.
        </s.TextDescription>
        <s.SpacerMedium />
      </s.Container>
    </s.Screen>
  );
}

export default App;
